@import "../variables";

.@{container-class} .app-header{
	position: relative;
	height: 1.92rem;
	line-height: 1.92rem;
	padding: 0 0.4rem;
	border-bottom: 0.5*@rex solid @border-color-base;
	background: #fff;

	> .title{
		font-size: 0.8rem;
		text-align: center;
		width: 100%;
		margin:0 auto;
		padding:0 2rem;
		font-weight: normal;
		.ellipsis();
	}

	.top-back{
		.absolute(0,_,_,0.4rem);
		font-size: 0.64rem;
		padding-left: .7rem;

		&:before,&:after{
			.size(0.7rem, 1*@rex);
			.absolute(50%,_,_,0);
			content: '';
			background: @text-color;
			transform-origin: 0 0;
		}

		&:before{
			transform: rotate(-45deg);
		}

		&:after{
			transform: rotate(45deg);
		}
	}

	.share{
		.absolute(0,0.4rem,_,_,);
		font-size: unit(@font-size-sm * 2, rem);
		line-height: 1.92rem;
		padding-right: .4rem;
	}
}
