@import "../mixins";

.hide {
	display: none !important;
}
.show {
	display: block !important;
}

/* clear */
.fn-clear { .clearfix(); }

/* float */
.fl { float: left; }
.fr { float: right; }

/* display */
.dn { display: none; }
.di { display: inline; }
.db { display: block; }
.dib { display: inline-block; }

/* font-style */
.fsn { font-weight: normal; font-style: normal; }
.fsb { font-weight: bold; }
.fsi { font-style: italic; }

/* text-align */
.tac { text-align: center; }
.tar { text-align: right; }
.tal { text-align: left; }
.taj { text-align: justify; }

/* visibility */
.vh { visibility: hidden; }
.vv { visibility: visible; }

/* overflow */
.ovh { overflow: hidden; }
.ova { overflow: auto; }

/* vertical-align */
.vam { vertical-align: middle; }
.vatb { vertical-align: text-bottom; }
.vab { vertical-align: bottom; }
.vat { vertical-align: top; }
.van { vertical-align: -2*@rex; }

.img-box{
	width: 100%;
	position: relative;

	&:before{
		content: "";
		display: block;
		width: 100%;
		padding-top: 100%;
	}

	& > .img{
		.absolute(0,_,_,0);
		.size(100%);
		max-height: 100%;
	}
}

.img100{ vertical-align: top;width: 100%;height: 100%; }
.pt10{ padding-top: 10*@rex; }
.pb10{ padding-bottom: 10*@rex; }
.pl10{ padding-left: 10*@rex; }
.pr10{ padding-right: 10*@rex; }
.mt10{ margin-top: 10*@rex; }
.mb10{ margin-bottom: 10*@rex; }
.ml10{ margin-left: 10*@rex; }
.mr10{ margin-right: 10*@rex; }

/* 块状元素水平居中 */
.auto { margin-left: auto; margin-right: auto; }

.inline-box{
	font-size:0;/* 所有浏览器 */
	*word-spacing:-1*@rex;/* IE6、7 */
}

.inline-any{
	font-size: @font-size-base;
	letter-spacing: normal;
	word-spacing: normal;
	vertical-align:top;
	display: inline-block;
	*display:inline;
	*zoom:1;
}

@media screen and (-webkit-min-device-pixel-ratio:0){
	/* firefox 中 letter-spacing 会导致脱离普通流的元素水平位移 */
	.inline-box{
		letter-spacing:-5*@rex;/* Safari 等不支持字体大小为 0 的浏览器, N 根据父级字体调节*/
	}
}
