@import "../variables";

.@{container-class}{
	.fn-price, .fn-price-sm, .fn-price-md, .fn-price-lg, .fn-price-xl{
		color: @price-color;
		&:before{ content: '\00a5' }

		.num{ margin-left: 2*@rex; }
	}

	.fn-price-sm{
		&:before, .point { font-size: @price-size-rmb-sm; }
		.num { font-size: @price-size-num-sm; }
	}

	.fn-price-md{
		&:before, .point { font-size: @price-size-rmb-md; }
		.num { font-size: @price-size-num-md; }
	}

	.fn-price-lg{
		&:before, .point { font-size: @price-size-rmb-lg; }
		.num { font-size: @price-size-num-lg; }
	}

	.fn-price-xl{
		&:before, .point { font-size: @price-size-rmb-xl; }
		.num { font-size: @price-size-num-xl; }
	}
}
